// frontend/src/components/layout/GlobalThemeToggle.tsx
'use client';

import { useState, useEffect } from 'react';
import { Sun, Moon } from 'lucide-react';

export default function GlobalThemeToggle() {
  const [isDark, setIsDark] = useState(false);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    // Synchroniser avec le th√®me actuel
    const savedTheme = localStorage.getItem('vibecro-theme');
    const systemPrefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    
    const currentIsDark = savedTheme === 'dark' || (!savedTheme && systemPrefersDark);
    setIsDark(currentIsDark);
    setMounted(true);
    
    // Appliquer imm√©diatement
    if (currentIsDark) {
      applyDarkThemeGlobally();
    }
  }, []);

  const applyDarkThemeGlobally = () => {
    // 1. Sur l'html
    document.documentElement.classList.add('dark');
    document.documentElement.setAttribute('data-theme', 'dark');
    document.documentElement.style.colorScheme = 'dark';
    
    // 2. Sur le body
    document.body.classList.add('dark-mode-active');
    document.body.style.backgroundColor = '#0a0a0a';
    document.body.style.color = '#ededed';
    
    // 3. Forcer sur TOUS les √©l√©ments
    const allElements = document.querySelectorAll('*');
    allElements.forEach(el => {
      // Ajouter la classe dark si l'√©l√©ment a des classes dark:
      const classes = Array.from(el.classList);
      const hasDarkVariant = classes.some(cls => cls.startsWith('dark:'));
      if (hasDarkVariant) {
        el.classList.add('dark');
      }
      
      // Transformer les classes light en dark
      if (el.classList.contains('bg-white')) {
        el.classList.add('dark:bg-gray-950');
        el.style.backgroundColor = '#0a0a0a';
      }
      if (el.classList.contains('text-gray-900')) {
        el.classList.add('dark:text-gray-100');
        el.style.color = '#f3f4f6';
      }
    });
    
    console.log('üåô Dark theme applied globally');
  };

  const applyLightThemeGlobally = () => {
    // 1. Sur l'html
    document.documentElement.classList.remove('dark');
    document.documentElement.setAttribute('data-theme', 'light');
    document.documentElement.style.colorScheme = 'light';
    
    // 2. Sur le body
    document.body.classList.remove('dark-mode-active');
    document.body.style.backgroundColor = '#ffffff';
    document.body.style.color = '#171717';
    
    // 3. Retirer dark de tous les √©l√©ments
    document.querySelectorAll('*').forEach(el => {
      el.classList.remove('dark');
      el.style.backgroundColor = '';
      el.style.color = '';
    });
    
    console.log('‚òÄÔ∏è Light theme applied globally');
  };

  const toggleTheme = () => {
    const newIsDark = !isDark;
    setIsDark(newIsDark);
    
    // Sauvegarder
    localStorage.setItem('vibecro-theme', newIsDark ? 'dark' : 'light');
    
    // Appliquer globalement
    if (newIsDark) {
      applyDarkThemeGlobally();
    } else {
      applyLightThemeGlobally();
    }
    
    console.log('Theme toggled to:', newIsDark ? 'dark' : 'light');
  };

  if (!mounted) {
    return (
      <div className="w-12 h-12 rounded-full bg-gray-200 animate-pulse" />
    );
  }

  return (
    <button
      onClick={toggleTheme}
      className="relative w-12 h-12 rounded-full bg-gray-100 dark:bg-gray-800 
                 flex items-center justify-center shadow-lg hover:shadow-xl
                 border border-gray-200 dark:border-gray-700
                 transition-all duration-300 hover:scale-105
                 group"
      aria-label={isDark ? "Passer en mode clair" : "Passer en mode sombre"}
      title={`Basculer en mode ${isDark ? 'clair' : 'sombre'}`}
    >
      {isDark ? (
        <Sun className="w-5 h-5 text-yellow-500 group-hover:rotate-180 transition-transform duration-500" />
      ) : (
        <Moon className="w-5 h-5 text-gray-700 group-hover:rotate-180 transition-transform duration-500" />
      )}
      
      <span className="absolute -bottom-1 -right-1 w-3 h-3 bg-green-500 rounded-full border-2 border-white dark:border-gray-900" />
    </button>
  );
}